.reason_main_text2 {
	margin-top: 55px;
	font-size: 17px;
	font-weight: 400;
	color: #1eacc7; }
.reason_main_text3 {
	margin-top: 54px;
	font-size: 17px;
	font-weight: 400;
	color: #1eacc7; }
.bg_mobileapp {
	min-height: 863px;
	background: url(../img/bg_Mesh_big.png) center center/cover no-repeat; }
.possibilities {
	width: 112px;
	height: 24px;
	background-color: #000000;
	font-size: 11px;
	font-weight: 100;
